@import url(https://fonts.googleapis.com/css?family=Poiret+One);
@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);


.mainw{
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.icon{
  font-size: 50px;
  padding: 5px;
  color:#3f206c;
  font-weight: bold;
}
.heading{
  background-color:#f0e3ff;
  margin: 10px;
  color:#3f206c;
  display:flex;
  justify-content: space-evenly;
  align-items: center;
  padding:10px;
  height:8vh;
  font-weight: bold;
}
@media(max-width:768px){
  .heading{
    display: block;
    text-align: center;
    height: 100%;
    padding:5px;
  }
}

input{
  padding: 10px;
  font-size: 20px;
  text-align: center;
  border-radius: 10px;
  border-color: #3f206c;
  width:40%;
}
.cards{
  display: flex;
  padding: 5px;
  justify-content:space-evenly;
  margin: 10px;
  width: 68%;
}
@media (max-width:768px) {
  .cards{
    display: block;
    width:100%;
    margin: 0px;
  }
  .card{
    min-width:22%;
    background-color: aqua;
    margin:1px;
  }
}

.card{
  min-width:37%;
  text-align: center;
  border-radius: 10px;
  background-color:#ffffff;
  margin: 20px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
  transition: 0.3s;
  padding:1px;
 
}
.card:hover{
  cursor: pointer;
  background-color: #3f206c;
}
.card:hover h1 {
  color: #ffffff;
}

.card:hover h2 {
  color: #ffffff;
}
.card:hover .icon {
  color: #ffffff;
}
.card:hover .temp{
  background-color: #ffffff;
  color: #3f206c;  
}
.temperature{
  font-size: 60px;
  color:#3f206c;
  font-weight: bold;
  line-height: 40px;
}
h2{
  font-size: 30px;
  color:#3f206c;
  font-weight: bold;
  line-height: 0px;
  text-transform: capitalize;
}
.temp{
  display: block;
  padding: 1px;
  font-size: 20px;
  background-color: #3f206c;
  margin-top:10px;
  color: #ffffff;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;

}

.box{
  display: flex;
  justify-content: center;
  height: 100%;
}
.box1 {
  background-color: #3f206c;
  color:#ffffff;
  width: 100%;
  border-radius: 10px;
  margin: 15px;
}
.box2 {
  text-align: left;
  background-color: #ffffff;
  color:#3f206c;
  width: 100%;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  margin: 15px;
}

.days{
  display: flex;
  justify-content:flex-start;
  margin: 10px;
  width: 65%;
  background-color: #ffffff;
  height: 31vh;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
}
.day1{
  width: 24%;
  border-radius: 10px;
  background-color:#3f206c;
  color: #ffffff;
  margin: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
  transition: 0.3s;
  padding:1px;  
  height: 28vh;
  text-align: center;
}
.day1:hover{
  background-color: #ffffff;
  color: #3f206c;
  cursor: pointer;
  transition:1s;
  transform: scaleY(1.1);
}

.day1 .icon{ 
  color: #ffffff;
  cursor: pointer;
}
.day1:hover .icon{ 
  color: #3f206c;
  cursor: pointer;
}
@media (max-width:768px) {
  .days{
    display: grid;
    grid-template-columns: auto auto;
    width: 95%;
    height: 60vh;
}
  .day1{
  width: 42vw;
  }
}
.otherdays{
  float:right;
  top:100px;
  right:0;
  position: absolute;
  text-align: center;
  width: 30%;
  margin:20px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
}
.otherday1{
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 10px;  
  margin: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
  transition: 0.3s;
  padding:1px; 
}
@media(max-width:768px){
  .otherdays{
    position: relative;
    width: 90%;
    top: 0px;
  }
}
.daysname{
  width: 100%;
}
.otemp{
  width: 50%; 
  text-align: left;
}
.otherday1 .icon{
  width: 100%;
}
.description{
  text-align: left;
  width: 80%;
  margin-left: 20px;
}



